<template>
    <div id="new-attraction">
        <h3>New Tag</h3>
        <div class="row">
            <form @submit.prevent="saveAttraction" class="col s12">
                
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="attraction_id" 
                        required>
                        <label>Attraction ID</label>
                    </div>
                </div>
                <button type="submit" class = "btn">Submit</button>
                <router-link to="/" class="btn 
                grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase'
var newAttractionID;
export default {
    name: 'new-attraction',
    data () {
        return {
            attraction_id: null,
            name: null,
            image_url: null,
            detail: null,
            tag: null
        }
    },
    methods: {
        
        saveAttraction () {
            db.collection('attractions').add({
                attraction_id: this.attraction_id,
                name: this.name,
                image_url: this.image_url,
                detail: this.detail,
                tag: this.tag
            })
            .then(docRef => this.$router.push('/'))
            .catch(error => console.log(err))
        }
    }
}

</script>

<style scoped>
.email {
    padding-right: 10px;
}
</style>


