import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewAttraction from '@/components/NewAttraction'
import ViewAttraction from '@/components/ViewAttraction'
import EditAttraction from '@/components/EditAttraction'
import Login from '@/components/Login'
import Register from '@/components/Register'
import NewTag from '@/components/NewTag'
import Home from '@/components/Home'
import SearchHistory from '@/components/SearchHistory'
import firebase from 'firebase'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/home',
      name: 'Home',
      component: Home,
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/history',
      name: 'SearchHistory',
      component: SearchHistory,
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requireGuest: true
      }
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta: {
        requireGuest: true
      }
    },
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard,
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/new',
      name: 'new-attraction',
      component: NewAttraction,
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/newtag',
      name: 'new-tag',
      component: NewTag,
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/edit/:attraction_id',
      name: 'edit-attraction',
      component: EditAttraction,
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/:attraction_id',
      name: 'view-attraction',
      component: ViewAttraction,
      meta: {
        requireAuth: true
      }
    }
  ]
});

// Nav Guard

router.beforeEach((to, from, next) => {
  // check for requireAuth guard
  if(to.matched.some(record => record.meta.requireAuth)) {
    // check if NOT logged in
    if(!firebase.auth().currentUser){
      // go to login
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      // proceed to route
      next();
    }
  } else if(to.matched.some(record => record.meta.requireGuest)) {
    // check if logged in
    if(firebase.auth().currentUser && firebase.auth().currentUser.email == "pattamue@gmail.com"){
      // go to login
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        }
      });
    } else if (firebase.auth().currentUser && firebase.auth().currentUser.email != "pattamue@gmail.com"){
      // go to login
      next({
        path: '/home',
        query: {
          redirect: to.fullPath
        }
      });
    }
    else {
      // proceed to route
      next();
    }
  } else {
    // proceed to route
    next();
  }
});

export default router;


