<template>
    <div id="home">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Search Attraction</h4></li>
        <div class="chip">
            ทำเล : ทะเล / ภูเขา  / น้ำตก / ป่า
        </div>
        <div></div>
        <div class="chip">
            ภาค : ภาคกลาง / ภาคเหนือ / ภาคใต้ / ภาคอีสาน
        </div>
        <div></div>
        <div class="chip">
            จำนวนนักท่องเที่ยว : คนน้อย / คนปานกลาง / คนมาก
        </div>
        <div></div>
        <div class="chip">
            ระยะทาง (จากกรุงเทพฯ เป็นกิโลเมตร) : 0-200 / 201-400 / 401-600 / 600+
        </div>
            <input v-model="tags" placeholder="write your tags here">
            <button class="btn btn-large grey lighten-4 
            black-text" @click="search(true)">Search</button>
            <li v-if="isSearch" v-for="attraction in attractions" v-bind:key="attraction.id"
            class="collection-item">
            {{attraction.attraction_id}} : {{attraction.name}}
            <div class="chip">{{attraction.tag}}</div>
            <router-link class="secondary-content" 
            v-bind:to="{name: 'view-attraction', params:{attraction_id : attraction.attraction_id}}">
            <i class="fa fa-eye"></i>"</router-link>
            </li>
        </ul>
    </div>
</template>

<script>
import db from './firebaseInit'
var i_search = false
export default {
    name: 'dashboard',
    data () {
        return {
            attractions: [],
            isSearch: i_search
        }
    },
    created () { // automatically run on reload
        db.collection('attractions').orderBy('attraction_id').get().then
        (querySnapshot => {
            querySnapshot.forEach(doc => {
                console.log(doc);
                const data = {
                    'id' : doc.id,
                    'attraction_id' : doc.data().attraction_id,
                    'image_url' : doc.data().image_url,
                    'name' : doc.data().name,
                    'tag' : doc.data().tag
                }
                this.attractions.push(data)
            })
        })
    },
    methods : {
        search(isSearch)
        {
            this.isSearch = isSearch
            console.log(isSearch)
            console.log(this.isSearch)
        }
    }
}
</script>


