<template>
    <nav >
        <div class="nav-wrapper green">
            <div class="container">
                <router-link to="/" class="brand-logo">
                Attraction Advisor </router-link>
                <ul class="right">
                    <li v-if="isLoggedIn"><span class="email black-text">{{currentUser}}</span></li>
                    <li v-if="isAdmin"><span class="email black-text">(Adminstrator)</span></li>
                    <li v-if="isLoggedIn"><router-link to="/home">Search Attraction</router-link></li>
                    <li v-if="isLoggedIn"><router-link to="/history">History</router-link></li>
                    <li v-if="isAdmin"><router-link to="/">Dashboard</router-link></li>
                    <li v-if="!isLoggedIn"><router-link to="/login">Login</router-link></li>
                    <li v-if="!isLoggedIn"><router-link to="/register">Register</router-link></li>
                    <li v-if="isLoggedIn"><button v-on:click="logout" class="btn black">Logout</button></li>
                </ul>
            </div>
        </div>
    </nav>
</template>

<script>
    import firebase from 'firebase'
    export default {
        name: 'navbar',
        data() {
            return {
                isLoggedIn: false,
                currentUser: false,
                isAdmin: false
            }
        },
        created() {
            if(firebase.auth().currentUser) {
                this.isLoggedIn = true
                this.currentUser = firebase.auth().currentUser.email
                if(firebase.auth().currentUser.email == "pattamue@gmail.com") {
                    this.isAdmin = true
                }
            }
        },

        methods: {
            logout: function() {
                firebase.auth().signOut().then(() => {
                    this.$router.push('/login')
                });
            }
        }
    }
</script>



