<template>
    <div id="home">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Search History</h4></li>
            <li v-for="attraction in attractions" v-bind:key="attraction.id"
            class="collection-item">
            "{{attraction.name}}"
            <router-link class="secondary-content" 
            v-bind:to="{name: 'view-attraction', params:{attraction_id : attraction.attraction_id}}">

            <i class="fa fa-eye"></i>"</router-link>
            </li>
        </ul>
    </div>
</template>

<script>
import db from './firebaseInit'
var i_search = false
export default {
    name: 'dashboard',
    data () {
        return {
            attractions: [],
            isSearch: i_search
        }
    },
    created () { // automatically run on reload
        db.collection('attractions').orderBy('name').get().then
        (querySnapshot => {
            querySnapshot.forEach(doc => {
                console.log(doc);
                const data = {
                    'id' : doc.id,
                    'attraction_id' : doc.data().attraction_id,
                    'image_url' : doc.data().image_url,
                    'name' : doc.data().name,
                    'tag' : doc.data().tag
                }
                this.attractions.push(data)
            })
        })
    },
    methods : {
        search(isSearch)
        {
            this.isSearch = isSearch
            console.log(isSearch)
            console.log(this.isSearch)
        }
    }
}
</script>


