<template>
    <div id="view-attraction">
        <ul class="collection with-header">
            <li class="collection-header"><h4>{{name}}</h4></li>
            <li class="collection-item">Attraction ID#: {{attraction_id}}</li>
            <li class="collection-item">Detail: {{detail}}</li>
            <li class="collection-item">Tag: {{tag}}</li>
            <li class="collection-item" style="display:block; margin:0 auto;"><img :src= image_url alt = "center image"/></li>
        </ul>
        <router-link to ="/home" class="btn grey">Back</router-link>
        <router-link v-if="isAdmin" v-bind:to="{name: 'edit-attraction',
        params:{attraction_id: attraction_id}}" class="btn green">Edit
        </router-link>
        <button v-if="isAdmin" @click="deleteAttraction" class="btn red">Delete</button>
    </div>
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase'
export default {
    name: 'view-attraction',
    data () {
        return {
            attraction_id: null,
            name: null,
            image_url: null,
            detail: null,
            tag: null,
            isAdmin: false
        }
    },
    beforeRouteEnter (to, from, next){
            var user = firebase.auth().currentUser;
            console.log(user.displayName)
        db.collection('attractions').where('attraction_id','==',to.params.attraction_id).get()
        .then(querySnapShot => {
            querySnapShot.forEach(doc => {
                next(vm => {
                    vm.attraction_id = doc.data().attraction_id
                    vm.name = doc.data().name
                    vm.image_url = doc.data().image_url
                    vm.detail = doc.data().detail
                    vm.tag = doc.data().tag
                })
            })
        })
    },
    created() {
        if(firebase.auth().currentUser) {
            this.isLoggedIn = true
            this.currentUser = firebase.auth().currentUser.email
            if(firebase.auth().currentUser.email == "pattamue@gmail.com") {
                this.isAdmin = true
            }
        }
    },
    watch:{
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('attractions').where
            ('attraction_id', '==', 
            this.$route.params.attraction_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.attraction_id = doc.data().attraction_id
                    this.name = doc.data().name
                    this.image_url = doc.data().image_url
                    this.detail = doc.data().detail
                    this.tag = doc.data().tag
                    
                })
            })
        },
        deleteAttraction() {
            if(confirm('Are you sure?')) {
                db.collection('attractions').where
                ('attraction_id', '==', 
                this.$route.params.attraction_id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete()
                        this.$router.push('/')
                    })
                })
            }
        }
    }
}
</script>

<style>
.action {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}
</style>


