<template>
    <div id="edit-attraction">
        <h3>Edit Attraction</h3>
        <div class="row">
            <form @submit.prevent="updateAttraction" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="attraction_id" 
                        required>
                        <label class = "active">Attraction ID#</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" 
                        required>
                        <label class = "active">Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="image_url" 
                        required>
                        <label class = "active">Image URL</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="detail" 
                        required>
                        <label class = "active">Detail</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="tag" 
                        required>
                        <label class = "active">Tag</label>
                    </div>
                </div>
                <button type="submit" class = "btn">Submit</button>
                <router-link to="/" class="btn 
                grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit-attraction',
    data () {
        return {
            attraction_id: null,
            name: null,
            image_url: null,
            detail: null,
            tag: null
        }
    },
beforeRouteEnter (to, from, next){
        db.collection('attractions').where('attraction_id','==',to.params.attraction_id).get()
        .then(querySnapShot => {
            querySnapShot.forEach(doc => {
                next(vm => {
                    vm.attraction_id = doc.data().attraction_id
                    vm.name = doc.data().name
                    vm.image_url = doc.data().image_url
                    vm.detail = doc.data().detail
                    vm.tag = doc.data().tag
                })
            })
        })
    },
    watch:{
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('attractions').where
            ('attraction_id', '==', 
            this.$route.params.attraction_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.attraction_id = doc.data().attraction_id
                    this.name = doc.data().name
                    this.image_url = doc.data().image_url
                    this.detail = doc.data().detail
                    this.tag = doc.data().tag
                })
            })
        },
        updateAttraction () {
            db.collection('attractions').where
            ('attraction_id', '==', 
            this.$route.params.attraction_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        attraction_id: this.attraction_id,
                        name: this.name,
                        image_url: this.image_url,
                        detail: this.detail,
                        tag: this.tag
                    })
                    .then(() => {
                        this.$router.push({name: 'view-attraction',
                        params: {attraction_id: this.attraction_id}})
                    })
                })
            })
        }
    }
}
</script>


